
html,
body {
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
}
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
}
.family-yahei {
  font-family: "Microsoft YaHei";
}
.family-yahei {
  font-family: "Arial";
}
.f8 {
  font-size: 8px;
}
.f10 {
  font-size: 10px;
}
.f12 {
  font-size: 12px;
}
.f14 {
  font-size: 14px;
}
.f18 {
  font-size: 18px;
}
.f26 {
  font-size: 26px;
}
.letter-spacing {
  letter-spacing: .2em;
}
.position-center {
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.position-middle {
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.pointer {
  cursor: pointer;
}
.white {
  color: #fff;
}
.blue {
  color: #2f2fed;
}
.gray {
  color: #969696;
}
.gray-2 {
  color: #8c8c8c;
}
img.fit {
  width: 100%;
}



